import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';

import { NotificationPage } from './notification';
import { CsdMarutiCarPage } from './maruti-csd/maruti-csd';
import { CsdHyundaiCarPage } from './hyundai-csd/hyundai-csd';

const routes: Routes = [
  {
    path: '',
    component: NotificationPage,
  },
  {
    path: 'maruti-csd-car-price',
    component: CsdMarutiCarPage,
   
  },
  {
    path: 'hyundai-csd-car-price',
    component: CsdHyundaiCarPage,
   
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class NotificationPageRoutingModule { }
